@import url(https://fonts.googleapis.com/css?family=Inter:300,400,500,600,700,800&display=swap);
.header-app{display:flex;position:relative;justify-content:space-between;align-items:center;padding:20px;height:60px;width:100%;background-color:var(--color-background-alt)}.header-app .title h1{margin-bottom:0px;font-size:var(--font-size-l);color:var(--color-text);font-weight:var(--font-weight-bold)}.header-app .buttons .button-user{display:flex;align-items:center;border-radius:var(--border-radius-base);padding:0.5rem}.header-app .buttons .button-user .name{font-size:var(--font-size-l);margin-left:10px}.header-app .buttons .button-user .triangle{margin-left:6px}.header-app .buttons .button-user .user-image{height:38px;width:38px;background-repeat:no-repeat;background-size:contain;border-radius:50%}.header-app .buttons .button-user:hover,.header-app .buttons .button-user.active{background-color:var(--color-background-alt-light);color:#fff;cursor:pointer}.header-app .user-menu{position:absolute;min-width:200px;top:66px;right:20px;transition:all 0.5ms ease-in-out;background-color:var(--color-background-alt-light);padding:0.5rem;border-radius:var(--border-radius-base);color:#fff}.header-app .user-menu .menu-title{font-weight:var(--font-weight);text-align:center;margin-top:10px}.header-app .user-menu>button{background-color:unset;font-weight:600;text-align:left;width:100%;margin:0px}.header-app .user-menu>button:hover{background-color:var(--color-background-alt-lighter)}.header-app .user-menu>button.danger{color:var(--color-red)}

.item-list{width:100%;position:relative;white-space:nowrap;border-collapse:collapse;max-height:100%}.item-list .table-header{background:none;border-radius:4px;height:40px}.item-list .table-header th{padding:0 10px;font-weight:500;color:#fff;position:-webkit-sticky;position:sticky;top:60px;background-color:var(--color-background);z-index:2}.item-list .table-header th i{margin-left:4px;color:var(--color-primary-light)}.item-list .table-header th i.unset{opacity:0.2;color:#fff}.item-list tr{width:100%;border-radius:19px;padding:0.5rem;height:24px;background-color:var(--color-background-alt-dark)}.item-list tr:first-of-type{border-top-left-radius:var(--border-radius-base);border-bottom-left-radius:var(--border-radius-base)}.item-list tr:last-of-type{border-top-right-radius:var(--border-radius-base);border-bottom-right-radius:var(--border-radius-base)}.item-list tr:nth-of-type(even){background-color:var(--color-background-alt)}.item-list tr:hover{background-color:var(--color-background-alt-light);cursor:pointer}.item-list tr .waveform{min-width:100px}.item-list tr td.center,.item-list tr th.center{text-align:center}.item-list tr td.sound-name,.item-list tr th.sound-name{width:300px;max-width:300px}.item-list tr td.sound-name .name-split,.item-list tr th.sound-name .name-split{display:flex;flex-direction:column;width:300px;max-width:300px}.item-list tr td.sound-name .name-split .name,.item-list tr th.sound-name .name-split .name{font-weight:var(--font-weight-bold)}.item-list tr td.sound-name .name-split .description,.item-list tr th.sound-name .name-split .description{font-size:var(--font-size-xs);font-weight:var(--font-weight);max-width:250px;overflow:hidden;text-overflow:ellipsis}

.filter-header{display:flex;align-items:center}.filter-header input[type='number']:first-of-type{margin-right:4px}.filter-header input[type='number']:last-of-type{margin-left:4px}.filter-header .filter-item{margin-right:12px;min-width:320px}.filter-header .filter-item:last-of-type{margin-right:unset}.filter-header .filter-item h3{font-size:--var(--font-size-s);margin-bottom:unset}.filter-header .filter-item .flex{display:flex;align-items:center;justify-content:space-between}

.home{min-height:100%;width:100%;overflow:visible;position:relative}.home .header-app{z-index:100;top:0;position:-webkit-sticky;position:sticky}.home .content{margin:1rem 2rem}

.login-page{display:flex;flex-direction:column;align-items:center;align-self:center;justify-content:center;width:100%;height:100%;background-color:var(--color-background)}.login-page .login-block{display:flex;flex-direction:column;justify-content:center;align-items:center;width:100%}.login-page .login-block form{display:flex;flex-direction:column;justify-content:center;align-items:center;width:100%}.login-page .login-block form input{margin-bottom:10px}.login-page .copy{display:flex;justify-content:center;align-items:center;flex-direction:column;position:absolute;bottom:25px;opacity:0.4}

.App{display:flex;background-color:var(--color-background);max-width:100%;min-height:100%}

:root{--color-background: #121212;--color-background-light: #2c2c2c;--color-background-lighter: #454545;--color-background-alt-darker: #121319;--color-background-alt-dark: #1d1f27;--color-background-alt: #282a36;--color-background-alt-light: #3e4153;--color-background-alt-lighter: #535871;--color-primary: #9a64ff;--color-primary-dark: #6441a5;--color-primary-light: #ad81ff;--color-information: #6264cf;--color-information-dark: #4143a5;--color-information-light: #5b5dbe;--color-warning: #e8a61a;--color-warning-dark: #bc8613;--color-warning-light: #edb848;--color-red: #ff2d2d;--color-text: #c4c4c4;--color-text-dark: #000;--color-content-background: #fff;--font-family: Inter,sans-serif;--font-weight: 400;--font-weight-bold: 700;--font-size-xs: 10px;--font-size-s: 12px;--font-size-m: 13px;--font-size-l: 16px;--font-size-xl: 20px;--border-radius-base: 8px}body,#root,html{margin:0;font-family:"Inter",sans-serif;font-weight:400;font-size:13px;color:#c4c4c4;color:var(--color-text);-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;height:100%;width:100%;margin:0;top:0;position:absolute;padding:env(safe-area-inset-top) 0 0}*{box-sizing:border-box}h1,h2,h3,h4,h5,h6{font-family:"Inter",sans-serif;font-weight:700;margin-top:unset}input:not([type]),input[type='text'],input[type='password'],input[type='email'],input[type='number'],textarea,input[type='checkbox']{padding:10px;font-size:13px;font-family:"Inter",sans-serif;max-width:350px;width:100%;border:1px solid rgba(0,0,0,0.25);border-radius:4px;background-color:#282a36;color:#9a64ff;margin-top:2px;margin-bottom:2px;font-weight:400}input:not([type]):disabled,input[type='text']:disabled,input[type='password']:disabled,input[type='email']:disabled,input[type='number']:disabled,textarea:disabled,input[type='checkbox']:disabled{border-color:rgba(255,255,255,0.15);color:rgba(0,0,0,0.25)}input:not([type]):focus,input[type='text']:focus,input[type='password']:focus,input[type='email']:focus,input[type='number']:focus,textarea:focus,input[type='checkbox']:focus{outline:2px solid #9a64ff}button{padding:10px 20px;min-width:150px;border:none;font-family:"Inter",sans-serif;border-radius:4px;background-color:#9a64ff;background-color:var(--color-primary);color:#fff}button:hover{background-color:#ad81ff;background-color:var(--color-primary-light);cursor:pointer}.bold{font-weight:700;font-weight:var(--font-weight-bold)}.info-box{max-width:350px;border-radius:4px;overflow:hidden;margin-bottom:20px}.info-box .header,.info-box .footer{padding:10px 15px;background-color:#6264cf;background-color:var(--color-information);color:#fff}.info-box .header span,.info-box .footer span{font-weight:700;font-weight:var(--font-weight-bold)}.info-box .header i,.info-box .footer i{margin-right:8px}.info-box .content{padding:10px 15px;background-color:#4143a5;background-color:var(--color-information-dark);color:#fff}.info-box .content p{margin-top:unset}.info-box .content p:last-of-type{margin-bottom:unset}.info-box.warning .header,.info-box.warning .footer{background-color:#e8a61a;background-color:var(--color-warning);color:#000;color:var(--color-text-dark)}.info-box.warning .content{background-color:#edb848;background-color:var(--color-warning-light);color:#000;color:var(--color-text-dark)}

